body {
    background: #e4dccb;
}

h2, h3 {
    font-family: serif;
}

.title, ul li {
    font-family: serif;
}

#header {
    border-radius: 50% 50% 0 0;
    background: #FCECDD;
    padding: 50px 50px 0 50px;
} 

#content {
    background: #FCECDD;
    padding: 10px 10px 40px 10px;
} 

.leave1 {
    max-width: 200px;
    width: 24%;
    height: 400px;
    background: url("../images/leave1.png") no-repeat left / contain;
    left: 0px;
    top : 8%;
    position:absolute;
}

.leave2 {
    max-width: 200px;
    width: 26%;
    height: 400px;
    background: url("../images/leave2.png") no-repeat right / contain;
    right: 0px;
    top : 5%;
    position:absolute;
}

.leave3 {
    max-width: 100px;
    width: 16%;
    height: 400px;
    background: url("../images/leave3.png") no-repeat left / contain;
    top : 30%;
    left: 0;
    position:absolute;
    opacity: 0.5;
}

.leave4 {
    max-width: 120px;
    width: 16%;
    height: 400px;
    background: url("../images/leave3.png") no-repeat left / contain;
    top : 90%;
    right: 0;
    position:absolute;
    transform: scaleX(-1);
    opacity: 0.5;
}

.leave5 {
    max-width: 130px;
    width: 18%;
    height: 400px;
    background: url("../images/leave2.png") no-repeat left / contain;
    top : 172%;
    left: 0;
    position:absolute;
    transform: scaleX(-1);
    opacity: 0.5;
}

@media (max-width: 800px) {
    .leave1 {
        width: 18%;
    }
    
    .leave2 {
        width: 16%;
    }

    .leave3 {
        width: 22%;
    }
  }
  